import type { ComponentType } from 'react';

import type { IconProps } from '@/icons';

type CardSize = 'base' | 'small' | 'large';

const SIZE_DIMENSIONS: Record<CardSize, { width: number; height: number }> = {
  base: { width: 350, height: 234 },
  small: { width: 300, height: 200 },
  large: { width: 420, height: 260 },
};

type DashboardCardProps = {
  icon: ComponentType<IconProps>;
  text: string;
  number: number | string;
  /** Accent color used for the icon background & number text. */
  color?: string;
  /** Preset size that scales the card; defaults to 350x234 (base). */
  size?: CardSize;
  className?: string;
};

export function DashboardCard({
  icon: Icon,
  text,
  number,
  color = 'var(--color-reserve)',
  size = 'base',
  className,
}: DashboardCardProps) {
  const dimensions = SIZE_DIMENSIONS[size];

  return (
    <article
      style={{ width: dimensions.width, height: dimensions.height }}
      className={`flex flex-col justify-between rounded-2xl bg-white p-6 shadow-sm ${className ?? ''}`.trim()}
    >
      <div className="flex items-center justify-between">
        <div
          className="flex h-12 w-12 items-center justify-center rounded-xl text-white"
          style={{ backgroundColor: color }}
        >
          <Icon size={24} color="currentColor" />
        </div>
        <span className="text-base font-medium text-gray-500">{text}</span>
      </div>

      <div>
        <p className="text-4xl font-semibold" style={{ color }}>
          {number}
        </p>
      </div>
    </article>
  );
}

